"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";

interface Product {
  id: number;
  name: string;
  image: string;
  price: string;
}

async function fetchProducts(category: string): Promise<Product[]> {
  return [
    { id: 1, name: `${category} Product 1`, image: "/sample1.jpg", price: "$50" },
    { id: 2, name: `${category} Product 2`, image: "/sample2.jpg", price: "$70" },
  ];
}

export default function CategoryPage() {
  const params = useParams();
  const category = Array.isArray(params?.category) ? params.category[0] : params?.category || "";
  const [products, setProducts] = useState<Product[]>([]);

  useEffect(() => {
    if (category) {
      fetchProducts(category).then(setProducts).catch(console.error);
    }
  }, [category]);

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold">Category: {category}</h1>
      <div className="grid grid-cols-3 gap-6 mt-6">
        {products.map((product) => (
          <div key={product.id} className="border p-4 rounded-md shadow-md">
            <img src={product.image} alt={product.name} className="w-full h-48 object-cover" />
            <h2 className="text-xl mt-2">{product.name}</h2>
            <p className="text-gray-600">{product.price}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
